<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
      <div class="row top-main">
        <div class="col-md-6 col-md-offset-2 mt-2" style="height: 51px;line-height: 51px">
          <div class="col-sm-12 mt-4" style="height:18px;font-size:18px;font-family:PingFang-SC-Bold;font-weight:bold;color:rgba(255,255,255,1);"><img :src="baseInfo.logoPath" style="border-radius: 50%;width: 51px;height: 51px;" v-if="baseInfo && baseInfo.logoPath"><span style="margin-left: 15px" v-if="baseInfo && baseInfo.teamName">{{baseInfo.teamName}} | {{baseInfo.projectName}}</span></div>
          <div class="col-sm-12 mt-6" style="font-family:PingFang-SC-Regular;font-weight:bold;color:rgba(255,255,255,1);">
            <span v-if="baseInfo && baseInfo.modeName">{{baseInfo.modeName}} |</span>
            <span v-if="baseInfo && baseInfo.memberName">成员  {{baseInfo.memberName}}  |</span>
            <span v-if="baseInfo && baseInfo.currentStageName && baseInfo.stepName">当前进度  {{baseInfo.currentStageName}}-{{baseInfo.stepName}} </span>
          </div>
        </div>
        <div class="col-md-2 text-center" style="border: 1px solid #FF4C55;border-radius: 10px;margin-top:100px;display: none"><i class="iconfont icongantanhao" style="color: #FF4C55"></i><span style="color: #FF4C55">即将超时任务：1项</span></div>
      </div>
      <div class="row" style="background:rgba(255,255,255,1);box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);height: 59px;line-height: 59px">
        <div class="col-md-3 col-md-offset-2"  style="cursor: pointer">
          <span @click="goBack(-2)">助教中心</span> > <span @click="goBack(-1)">项目列表</span> > 项目
        </div>
      </div>
      <div class="row pt-4 mt-3">
        <div class="col-md-2 col-md-offset-2 task-list">
          <div class="bar">
            <span>阶段与任务列表</span>
          </div>
          <!--<StepList :data="stepData" :expand-id="currentStepId" :active-id=81 :iconshijian-flg=true></StepList>-->
          <StepList v-if="stepData" @activeItem="activeItemList" :data="stepData" :expand-id="currentStageId" :active-id="currentStepId" style="min-height: 600px"></StepList>
        </div>
        <div class="col-md-6 ml-5">
          <div class="bar">
            <span>任务详情</span>
          </div>
          <div class="col-md-12 pt-4 task-detail">
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconrenwumingcheng "></i><span class="ml-3 font-bold">任务名称：</span><span class="font-regular ml-3" v-if="taskInfo && taskInfo.missionName">{{taskInfo.missionName}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconshijian "></i><span class="ml-3 font-bold">点评截止时间：</span><span class="font-regular ml-3" v-if="taskInfo && taskInfo.missionDeadline">{{taskInfo.missionDeadline}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconwendangmoban "></i><span class="ml-3 font-bold">任务描述：</span>
              <span class="font-regular col-md-12 pl-5 mt-2" v-if="taskInfo && taskInfo.missionDescribe">{{taskInfo.missionDescribe}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconrenwuyaoqiu "></i><span class="ml-3 font-bold">任务要求：</span>
              <span class="font-regular col-md-12 pl-5 mt-2" v-if="taskInfo && taskInfo.missionRequirement">{{taskInfo.missionRequirement}}</span>
            </div>
          </div>
          <div class="col-md-12 mt-4 history">
            <div class="col-md-12 pl-0 pr-0" style="height: 55px;line-height: 50px;border-bottom:1px solid #EBEBEB">
              <div class="col-md-2"  style="cursor: pointer" @click="changePageStatus('history')">
                <span class="col-sm-12 text-center font-tab" :style="{ color: fontColor('history') }"  >点评历程</span>
                <span v-if="pageStatus==='history'" class="col-sm-4 col-sm-offset-4 text-center active-bg-color" style="height:2px;border-radius:1px;"></span>
              </div>
              <div class="col-md-2"  style="cursor: pointer" @click="changePageStatus('Qa')">
                <span class="col-sm-12 text-center font-tab" :style="{ color: fontColor('Qa') }" >Q&A</span>
                <span v-if="pageStatus==='Qa'" class="col-sm-4 col-sm-offset-4 text-center active-bg-color" style="height:2px;border-radius:1px;"></span>
              </div>
            </div>
            <div class="col-md-12" v-if="pageStatus === 'history'">
              <div class="col-md-12 text-center doc mt-3 mb-5" v-for="obj in docData" v-bind:key="obj.outputId">
                <!--<i v-show="obj.commentStatus == '-1'" class="iconfont iconyiwancheng icon" style="color: #00C366"></i>-->
                <!--<i v-show="obj.commentStatus == '1'" class="iconfont iconweiwancheng icon" style="color: #FF4C55"></i>-->
                <!--<i v-show="obj.commentStatus == '0'" class="iconfont iconwenhao icon" style="color: rgb(252, 151, 1)"></i>-->
                <div class="col-md-1">
                  <img src="../../../assets/img/university.png" style="width: 36px;height:42px;margin-top: 9px">
                </div>
                <div class="col-md-8 text-left">
                  <span class="col-md-12 font-regular" style="font-size: 12px;margin-top: 12px;">{{obj.outputName}}</span>
                  <span  class="col-md-5 font-regular" style="font-size: 12px">上传时间：{{obj.uploadTime}} </span>
                  <span  class="col-md-4 font-regular text-center pl-0" style="font-size: 12px">操作：{{obj.realName}} </span>
                </div>
                <div class="col-md-3 text-left mt-5">
                  <span v-show="obj.commentStatus == '-1'" class="font-regular" style="color: #FC4E54;font-size: 12px;margin-top: 36px;">未通过</span>
                  <span v-show="obj.commentStatus == '0'" class="font-regular" style="color: rgb(252, 151, 1);font-size: 12px;margin-top: 36px;">待点评</span>
                  <span v-show="obj.commentStatus == '1'" class="font-regular" style="color: #02C365;font-size: 12px;margin-top: 36px;">已通过</span>
                  <span v-show="obj.commentStatus == '2'" class="font-regular" style="color: rgb(252, 151, 1);font-size: 12px;margin-top: 36px;">完成未通过</span>
                  <span v-show="obj.commentStatus == '0' && obj.commentType == '1'" class="font-regular" style="font-size: 12px;margin-top: 36px;margin-left: 20px;cursor: pointer;" @click="gotoComment(obj)">去点评</span>
                  <span v-show="obj.commentStatus == '2' && obj.commentType == '0' && obj.interveneSwitch == '1'" class="font-regular" style="font-size: 12px;margin-top: 36px;margin-left: 20px;cursor: pointer;" @click="gotoSystemComment(obj)">去点评</span>
                </div>
                <div v-if="obj.commentType == '0' && obj.commentStatus == '-1'">
                  <div v-for="(codeCheckObj, index) in obj.systemCommentDetail" v-bind:key="index">
                    <div class="col-xs-12 col-md-12 codecheck-box">
                      <div class="col-md-2 col-md-offset-1 col-xs-12 pr-0">
                        <a v-show="codeCheckObj.commentCode=='CODE_DUPLICATION'" class="comment-size-name"> 文档查重 : </a>
                        <a v-show="codeCheckObj.commentCode=='CODE_STYLE'" class="comment-size-name"> 代码规范检查 : </a>
                        <a v-show="codeCheckObj.commentCode=='CODE_LOGIC'" class="comment-size-name"> 代码逻辑检查 : </a>
                      </div>
                      <div class="col-xs-12 col-md-2 pull-left" style="font-size: 13px;">
                        <span style="color: #02C365" class="my-pl-con pull-left" v-show="codeCheckObj.systemCommentStatus==1">已通过</span>
                        <span style="color: rgba(255,76,85,1)" class="my-pl-con pull-left" v-show="codeCheckObj.systemCommentStatus==-1">未通过</span>
                        <span style="color: #FF9600" class="my-pl-con pull-left" v-show="codeCheckObj.systemCommentStatus==0">未点评</span>
                        <span style="color: #409eff;" class="my-pl-con pull-left" v-show="codeCheckObj.systemCommentStatus==2">部分通过</span>
                      </div>
                      <div class="col-xs-12 col-md-3 pull-left text-left">
                        <a v-show="codeCheckObj.systemCommentStatus!=0" style="font-size: 12px;color:#666666">查看详情</a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-12 comment-box" v-if="pageStatus === 'Qa'">
              <div v-for="(item,index) in commentTree" v-bind:key="index">
                <div  class="comment-per col-md-12 col-xs-12">
                  <div class="col-md-2">
                    <img :src="item.userPhotoUrl" style="width: 100%;border-radius: 50%">
                  </div>
                  <div class="comment col-md-10" >
                    <div class="font-bold" v-if="item.commentId === null || item.commentId === ''">
                      {{item.discussantName}}&nbsp;<span>{{item.commentTime}}</span>
                      <div class="pointer" style="float: right;margin-right: 20px;"><a @click="showFatherCommentArea(item)"><i class="icondazhongicon05 iconfont font-bold font-large" :style="{ color: item.iconColor }"></i>&nbsp;<span :style="{ color: item.iconColor, fontWeight: '300', fontSize: '12px' }">回复</span></a></div>
                    </div>
                    <p v-if="item.commentId === null || item.commentId === ''" v-html="item.content">
                      <span v-if="item.stepName">#{{item.stepName}}</span>
                      <span v-if="item.materialName">#{{item.materialName}}</span>
                      {{item.content}}
                    </p>
                    <div v-if="commentReplyFlg" class="mt-3 mb-3">
                      <textarea v-if="item.newCommentReply" :placeholder="item.place" class="el-textarea__inner" v-model="item.newCommentReplyContent" style="min-height: 100px"></textarea>
                      <div v-if="item.newCommentReply" class="new-reply-btn pointer" @click="submitFatherComment(item)"><span>发布</span></div>
                    </div>
                    <div v-if="item.reply.length > 0" class="reply-list">
                      <CommentList :comment="item" @submitSonComment="submitSonComment"></CommentList>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    </div>
</template>

<script>
import Header from '../../../components/assistant/Header.vue'
import StepList from '../../../components/school-training/TeamStepList.vue'
import getQueryString from '@/utils/getQueryString'
import CommentList from '../../../components/learning-library/commentList.vue'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header,
    StepList,
    CommentList
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      baseInfo: null,
      stepData: null,
      docData: [],
      teamId: '',
      projectId: '',
      currentStepId: -1,
      currentStageId: -1,
      facultyMarjorId: '',
      stepInfo: [],
      taskInfo: null,
      currentStageName: '',
      memberName: '',
      pageStatus: 'history',
      commentReplyFlg: false,
      commentTree: null
    }
  },
  methods: {
    loadData () {
      let me = this
      const loading = Loading.service({
        lock: true,
        text: '加载中',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)',
        color: '#6612cf'
      })
      me.teamId = parseInt(getQueryString('teamId'))
      me.projectId = parseInt(getQueryString('projectId'))
      me.currentStepId = parseInt(getQueryString('currentStepId'))
      me.facultyMarjorId = parseInt(getQueryString('facultyMarjorId'))
      me.$http.post('/doproject/steplist', { 'teamid': this.teamId }, res => {
        if (res.success) {
          for (let obj1 of res.data.stages) {
            obj1.iconShow = false
            let k = 0
            for (let obj2 of obj1.steps) {
              if (obj2.status === 2) {
                k++
              }
              if (obj2.status === 3 || k === obj1.steps.length) {
                obj1.iconShow = true
                break
              }
            }
          }
          me.stepData = res.data.stages
          me.currentStageId = res.data.currentStageId
          for (let obj of me.stepData) {
            if (obj.stageId === me.currentStageId) {
              me.currentStepId = obj.steps[0].stepId
              me.currentStageName = obj.stageName
            }
          }
          me.$http.post('/assistant/teammissionlist', {
            'teamId': me.teamId,
            'currentStepId': me.currentStepId,
            'projectId': me.projectId,
            'facultyMarjorId': me.facultyMarjorId
          }, res => {
            if (res.success) {
              // me.stepData = res.data.stagesteplist
              for (var i = 0; i < res.data.initproject.length; i++) {
                res.data.initproject[i].uploadTime = res.data.initproject[i].uploadTime.substr(0, 10)
                res.data.initproject[i].outputNameReal = res.data.initproject[i].outputName
                var outputNameList = res.data.initproject[i].outputName.split('$_$')
                res.data.initproject[i].outputName = outputNameList[1]
              }
              me.docData = res.data.initproject
              me.taskInfo = res.data.initmsg[0]
              me.memberName = res.data.initmsg[0].memberName
              me.loadDiscussionTreeInfo()
              setTimeout(() => {
                loading.close()
              }, 1000)
            }
          })
        }
      })
      me.$http.post('/assistant/selectTeamProgressInfo', { teamId: me.teamId, currentStepId: me.currentStepId, projectId: me.projectId }, res => {
        if (res.success) {
          if (res.data.mode === 0) {
            res.data.modeName = '单人模式'
          } else {
            res.data.modeName = '团队模式'
          }
          me.baseInfo = res.data
          localStorage.setItem('assTeamInfo', JSON.stringify(res.data))
        }
      })
    },
    // 加载评论
    loadDiscussionTreeInfo () {
      let me = this
      this.$http.post('/doproject/discussionTreeInfo', {
        'projectId': me.projectId
      }, res => {
        if (res.success) {
          me.commentTree = res.data
          console.log(me.commentTree)
        }
      })
    },
    activeItemList (index, name) {
      let me = this
      me.currentStepId = name
      const loading = Loading.service({
        lock: true,
        text: '加载中',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)',
        color: '#6612cf'
      })
      me.$http.post('/assistant/teammissionlist', {
        'teamId': me.teamId,
        'currentStepId': me.currentStepId,
        'projectId': me.projectId,
        'facultyMarjorId': me.facultyMarjorId
      }, res => {
        if (res.success) {
          // me.stepData = res.data.stagesteplist
          for (var i = 0; i < res.data.initproject.length; i++) {
            res.data.initproject[i].uploadTime = res.data.initproject[i].uploadTime.substr(0, 10)
            res.data.initproject[i].outputNameReal = res.data.initproject[i].outputName
            var outputNameList = res.data.initproject[i].outputName.split('$_$')
            res.data.initproject[i].outputName = outputNameList[1]
          }
          me.docData = res.data.initproject
          console.log(me.docData)
          me.taskInfo = res.data.initmsg[0]
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      })
    },
    showFatherCommentArea (obj) {
      this.commentReplyFlg = false
      if (obj.newCommentReply === null || obj.newCommentReply === undefined) {
        obj.newCommentReply = true
        obj.place = '回复' + obj.discussantName
        obj.newCommentReplyContent = null
        obj.iconColor = '#6612cf'
      } else {
        obj.newCommentReply = !obj.newCommentReply
        if (obj.newCommentReply === false) {
          obj.iconColor = 'black'
        } else {
          obj.iconColor = '#6612cf'
        }
      }
      this.commentReplyFlg = true
    },
    submitSonComment (data) {
      let me = this
      let date = new Date()
      date = me.getNowTime(date)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': date,
        'content': data.newReplyContent,
        'projectId': me.projectId,
        'commentId': data.commentId,
        'stepId': me.currentStepId
      }, res => {
        me.$message('发布成功')
        me.loadDiscussionTreeInfo()
      })
    },
    submitFatherComment (obj) {
      let me = this
      let date = new Date()
      date = me.getNowTime(date)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': date,
        'content': obj.newCommentReplyContent,
        'projectId': me.projectId,
        'commentId': obj.id,
        'stepId': me.currentStepId
      }, res => {
        // console.log(res)
        me.$message('发布成功')
        me.loadDiscussionTreeInfo()
      })
    },
    changePageStatus (status) {
      this.pageStatus = status
    },
    fontColor (color) {
      if (this.pageStatus === color) {
        return '#6612cf'
      } else {
        return 'black'
      }
    },
    getNowTime (date) {
      var Y = date.getFullYear() + '-'
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-'
      var D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' '
      var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':'
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':'
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let dateStr = Y + M + D + h + m + s
      return dateStr
    },
    goBack (index) {
      window.history.go(index)
    },
    gotoComment (obj) {
      localStorage.setItem('outputName', obj.outputNameReal)
      localStorage.setItem('facultyMarjorId', this.facultyMarjorId)
      localStorage.setItem('projectId', this.projectId)
      window.location = 'assistant-task-comment.html?outputId=' + obj.outputId + '&teamId=' + this.teamId + '&currentMissionId=' + this.currentStepId + '&memberCode=' + obj.uploadUserId
    },
    gotoSystemComment (obj) {
      localStorage.setItem('outputName', obj.outputNameReal)
      localStorage.setItem('facultyMarjorId', this.facultyMarjorId)
      localStorage.setItem('projectId', this.projectId)
      window.location = 'assistant-task-comment.html?outputId=' + obj.outputId + '&teamId=' + this.teamId + '&currentMissionId=' + this.currentStepId + '&memberCode=' + obj.uploadUserId + '&interveneSwitch=' + obj.interveneSwitch
    }
  }
}
</script>
<style scoped lang="scss">
  @import "../../../assets/css/bootstrap.css";
  @import "../../../assets/css/theme.css";
  @import "../../../assets/css/text.css";
  .top-main{
    background-image: url(../../../assets/img/assistant_bg.png);
    background-size: 100% 100%;
    height: 138px;
  }
  .active-font-color{
    font-size:16px;
    font-family:PingFang-SC-Bold;
    font-weight:bold;
    color: #6612CF;
  }
  .unactive-font-color{
    height:16px;
    font-size:16px;
    font-family:PingFang-SC-Regular;
