    color:rgba(102,102,102,1);
  }
  .history{
    min-height:493px;
    background:rgba(255,255,255,1);
    box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);
    border-radius:10px;
    padding: unset;
  }
  .doc{
    min-height:64px;
    background:rgba(248,248,248,1);
    border:1px solid rgba(235, 235, 235, 1);
    border-radius:5px;
  }
  .icon{
    position: absolute;
    top:0;
    right:0;
    margin-top:-4px;
  }
  .reply-list{
    padding: 20px;
  }
  .comment-box{
    padding: 20px;
    max-height: 550px;
    overflow: auto;
  }
  .comment-per{
    border-bottom: 1px solid #ddd;
    margin: 10px;
    padding: 10px;
  }
  .comment{
  p{
    padding-top: 10px;
    padding-bottom: 10px;
  }
  padding: 20px;
  .reply-list{
    background-color: #ddd;
    padding: 20px;
  }
  }
  .codecheck-box{
    margin-top: 10px;
    margin-bottom:20px;
  }
  .comment-size-name{
    font-size: 13px;
    font-family: PingFang-SC-Regular;
    font-weight: 400;
    color: #666666;
    float:left;
  }
</style>
<style>
  .el-tabs__item:hover, .el-tabs__item.is-active {
    color: #6612CF!important;
    cursor: pointer;
  }
  .el-tabs__active-bar{
    background-color: #6612CF!important;
  }
  .el-input--suffix .el-input__inner {
    background: #F8F8F8!important;
